.chat {
  width: 100%;
  height: 100%;
  background-color: white;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  overflow: scroll;
}

.chat__message {
  display: grid;
}

.chat__messages {
  padding: 0 15px;
  height: 100%;
  /*display: grid;
  grid-template-rows: min-content;
  grid-auto-rows: min-content; 
  align-content: end;*/
  overflow: auto;
  background-position: bottom;
}

.chat__message-block {
  padding: 5px 10px;
  max-width: 70%;
  align-self: start;
  margin-bottom: 9px;
  border-radius: 16px;
}

.chat__message-block_otherIs_true {
  background-color: #e7e7e7;
  justify-self: start;
}

.chat__message-block_otherIs_false {
  background-color: #99CCFF;
  justify-self: end;
}

.chat__message-text {
  word-wrap: break-word;
}

.chat__message-date {
  text-align: center;
  margin: 5px 0;
}

.chat__input {
  width: 100%;
  border-top: 1px solid grey;
  display: grid;
  grid-template-columns: 1fr 30%;
}

.chat__enter-block {
  display: grid;
  align-self: center;
  padding: 8px 15px;
}

.chat__enter {
  align-self: end;
  width: 100%;
  display: block;
  resize: none;
  box-sizing: border-box;
  min-height: 16px;
  max-height: 64px;
  height: 16px;
  font-size: 16px;
  border: none;
  outline: none;
}

.chat__send {
  align-self: end;
  border: none;
  outline: none;
  background-color: inherit;
  font-weight: 600;
  font-size: 14px;
  padding: 10px 5px;
  margin: 5px 0;
}
