.profile {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  background-color: #EBEBEB;
}

.profile__account {
  padding: 70px 0 50px 0;
  display: grid;
  grid-template-rows: repeat(2, min-content);
  row-gap: 40px;
  background-color: white;
  border-radius: 0% 0% 90% 90% / 10% 90% 15% 15% ;
  box-shadow: 0px 0px 20px 6px rgba(0, 0, 0, 0.6);
}

.profile__account-user {
  justify-self: center;
  display: grid;
  grid-template-rows: repeat(2, min-content);
  row-gap: 10px;
}

.profile__account-user-avatar {
  justify-self: center;
  width: 90px;
  height: 90px;
  border-radius: 49%;
  object-fit: cover;
  object-position: center;
}

.profile__account-user-name {
  justify-self: center;
  font-weight: 500;
  font-size: 22px;
}

.profile__settings {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin: 0 20px;
}

.profile__setting {
  justify-self: center;
  text-decoration: none;
  display: grid;
  row-gap: 6px;
}

.profile__setting_middle {
  margin-top: 20px;
}

.profile__setting-button {
  align-self: center;
  justify-self: center;
  display: grid;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  box-shadow: 0px 3px 6px 1px rgba(0, 0, 0, 0.6);
}

.profile__setting-button-image {
  align-self: center;
  justify-self: center;
  width: 30px;
  height: 30px;
}

.profile__setting-text {
  color: black;
  text-align: center;
  font-weight: 700;
  font-size: 13px;
  text-transform: uppercase;
}

