@import url(./_disabled/form__input_disabled.css);
@import url(./_inactive/form__input_inactive.css);

.form__input {
  background-color: #F4F4F4;
  border-radius: 8px;  
  border: 1px solid #F4F4F4;
  outline: none;
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 16px;
  padding: 15px;
  box-sizing: border-box;
}